import React, { Component } from 'react';
// import { Link, Redirect } from 'react-router-dom';
import { Button, Card, CardBody, CardGroup, Col, Container, Form, Row } from 'reactstrap';
import { withFirebase } from 'Helpers/Firebase';
import { USERS } from 'constants/Constants';
import { emailKhongDuocCapQuyen } from 'constants/Messages';

class Login extends Component {
  state = {
    username: '',
    password: '',
  } 

  logInHandle = () => {
    this.props.firebase.doLogIn().then(res => {
      const email = res.user.email;
      const isAuthed = USERS[email];
      if(isAuthed) {
        this.props.history.push('/');
      } else {
        alert(emailKhongDuocCapQuyen)
        this.props.firebase.doLogOut();
      }
    });
}

  onChangeHandle = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    })
  }

  render() {
    return (
      <div className="app flex-row align-items-center">
        <Container>
          <Row className="justify-content-center">
            <Col md="8">
              <CardGroup>
                <Card className="p-4">
                  <CardBody>
                    <Form>
                      <h1>Đăng nhập</h1>
                      <p className="text-muted">Đăng nhập bằng tài khoản gmail</p>
                      <Row>
                        <Col xs="6">
                          <Button
                            color="primary"
                            className="px-4"
                            onClick={this.logInHandle}
                          >Đăng nhập</Button>
                        </Col>
                      </Row>
                    </Form>
                  </CardBody>
                </Card>
              </CardGroup>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default withFirebase(Login);
